<template>
  <v-app
    :dark="isDark"
  >
    <core-toolbar />

    <core-drawer />

    <core-view />
    <!-- I'm adding in a night mode switcher on the main page, which shows up in the form of a button in the bottom
    right corner of every screen. The "fixed" option lets it be in the same place, no matter what. -->
    <v-speed-dial
      bottom
      right
      fixed
      direction="top"
      small
    >
      <!-- You need the activator so that it turns into a button you can interact with. We encompass it so that it
      looks nicer than the default button option. -->
      <template v-slot:activator>
        <v-btn
          color="#800000"
        >
          <v-icon>fa-cog</v-icon>
        </v-btn>
      </template>
      <v-btn
        color="#800000"
        @click="switchDark"
      >
        <v-icon>fas fa-moon</v-icon>
      </v-btn>
      <router-link
        v-ripple
        class="toolbar-items"
        to="/Home"
      >
        <v-btn
          color="#800000"
        >
          <v-icon>mdi-view-dashboard</v-icon>
        </v-btn>
      </router-link>
    </v-speed-dial>
  </v-app>
</template>

<script>
export default {
  data: () => ({
    // Initializing the isDark option that lets us switch between dark and light mode.
    isDark: false
  }),
  methods: {
    // Dark mode switcher function.
    switchDark () {
      this.isDark = !this.isDark
    }
  }
}
</script>

<style lang="scss">
@import '@/styles/index.scss';

/* Remove in 1.2 */
.v-datatable thead th.column.sortable i {
  vertical-align: unset;
}
</style>
